<template>
  <!-- <Card style="width:320px">
    <div style="text-align:center">
       <p slot="title">旅游助手</p>
      <img width="300" src="https://s2.ax1x.com/2019/10/09/uIGryn.jpg" />
      <h3>A high quality UI Toolkit based on Vue.js</h3>
    </div>
  </Card>-->

  <div>
    <AppbarClassify :vdata="vdata" v-on:tab-click="listenTab" />
    <v-content>
      <v-container class="pa-2" fluid>
        <v-row>
          <v-col
            style="padding:10px 20px;"
            v-for="item in classifyData"
            :key="item._id"
            cols="12"
            xs="12"
            sm="6"
            md="4"
            lg="4"
            xl="3"
          >
            <v-card class="mx-auto" max-width="400" @click="toDetails(item)" :ripple="false">
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title class="headline">{{item.title}}</v-list-item-title>
                  <v-list-item-subtitle>{{item.subtitle}}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
              <v-img class="block--text" height="500" contain :src="item.images[0]"></v-img>

              <v-card-text>
                <span class="orange--text subtitle-1">价格:{{item.price}}VB</span>
                <br />
                <span>设计+说明文档</span>
                <v-chip-group column>
                  <v-chip v-for="(itemTag,index) in item.tags" :key="index">{{itemTag}}</v-chip>
                </v-chip-group>
              </v-card-text>

              <v-card-actions>
                <v-btn text color="blue" @click.stop="share(item.id)">分享</v-btn>
                <v-btn text color="blue">下载</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>

        <v-row align="center" justify="center" v-if="classifyData.length==0?true:false">
          <v-col class="text-center">
            <p class="subtitle-1">该分类下暂时还没有东西！</p>
            <v-btn depressed color="primary">点我定制</v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-content>

    <v-dialog v-model="shareDialog" max-width="500px">
      <v-card>
        <v-card-title class="headline">分享设计</v-card-title>

        <!-- <v-card-text>Let Google help apps determine location. This means sending anonymous location data to Google, even when no apps are running.</v-card-text> -->
        <v-text-field style="margin:0 10px" v-model="link" label="设计地址" single-line></v-text-field>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green darken-1" text @click="shareDialog = false">取消</v-btn>

          <v-btn
            color="green darken-1"
            text
            v-clipboard:copy="link"
            v-clipboard:success="onCopy"
          >复制链接</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar v-model="snackbar">
      已成功复制到你的剪贴板
      <v-btn color="red" text @click="snackbar = false">关闭</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import data from "../data.json";
import AppbarClassify from "@/components/appbar-classify.vue";

export default {
  name: "home",
  components: { AppbarClassify },
  data() {
    return {
      vdata: {},
      allData: [],
      classifyData: [],
      shareDialog: false,
      link: "",
      snackbar: false
    };
  },
  methods: {
    onCopy() {
      this.snackbar = true;
       this.shareDialog = false;
    },

    share(id) {
      this.shareDialog = true;
      this.link = "https://"+window.location.host + "/details/" + id;
    },
    download() {},
    toDetails(item) {
      this.$router.push({ path: "/details/" + item.id });
    },
    listenTab(item) {
      //console.info("父:" + item);
      if (item != "ALL") {
        if (this.allData != null) {
          this.classifyData = this.allData.filter(
            x => x.language == item.toLowerCase()
          );
        }

        console.info(this.allData);
        console.info(this.classifyData.length);
      } else {
        this.classifyData = this.allData;
      }
    },
    loadData() {
      this.axios
        .get("classify/" + this.vdata.alias)
        .then(response => {
          this.allData = response.data;
          this.classifyData = this.allData;
          //console.info(response);
        })
        .catch(response => {
          console.log(response);
        });
    }
  },
  mounted() {
    // this.$store.dispatch("tabsVisibleAction", true);

    this.vdata = data.programTypeList.find(
      x => x.alias === this.$route.params.id
    );

    this.loadData();
  },
  watch: {
    vdata(newData, oldData) {
      //vdata属性改变在这里
    }
  },
  computed: {}
};
</script>
